.card__image {
  margin-bottom: 25px;
  width: 100%;
  aspect-ratio: 1;
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .card__image {
    margin-bottom: 15px;
  }
}

@media screen and (min-width: 320px) and (max-width: 450px) {
  .card__image {
    margin-bottom: 25px;
  }
}